#home-background {
  position: relative;
  background: url("../img/home_background.jpg") no-repeat center;
  background-size: cover;
}

#home-background {
  padding-top: 12em;
  padding-bottom: 12em;
}

#home-content h1 {
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}

#home-content p {
  -webkit-transform: translateY(-40%);
      -ms-transform: translateY(-40%);
          transform: translateY(-40%);
}

#home-content p a {
  -webkit-transform: translateY(50%);
      -ms-transform: translateY(50%);
          transform: translateY(50%);
}

.navbar-register-link a {
  text-decoration: none;
  color: #28a745;
}

#tutorial {
  background: -webkit-gradient(linear, left top, right top, from(black), color-stop(grey), to(black));
  background: -webkit-linear-gradient(left, black, grey, black);
  background: -o-linear-gradient(left, black, grey, black);
  background: linear-gradient(90deg, black, grey, black);
}

.carousel-inner {
  width: 65%;
  height: 65%;
  margin: 0 auto;
}

.carousel-caption p {
  font-size: 2em;
}

.carousel-control-prev-icon, .carousel-control-next-icon {
  height: 4em;
  width: 4em;
}


