#not-found {
  position: relative;
  height: 30em;
}

/* Centering both horizontally and vertically */
#not-found-content {
  width: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

/* Select a class with names: .fa, .fa-frown-o AND .fa-lg */
.fa.fa-frown-o.fa-lg {
  color: #CD202C;
}


